<template lang="pug">
.my-messages

  Appbar

  v-tabs.elevation-0(dark v-model="currentTab" color="primary")
    // @TODO: Paths don't matter here, only route names. Make issue to Vuetify.
    v-tab(v-bind:to="{ name: 'messages' }" ripple) My Messages
    v-tab(v-bind:to="{ name: 'messages' }" ripple) Thumbs Up
    v-tab(v-bind:to="{ name: 'messages' }" ripple) Thumbs Down

  .my-messages__hero
    img.my-messages__cone(src='~/@/assets/images/traffic-cone.svg' alt='Under Construction')
    div
      |work-in-progress

</template>

<script>
export default {
  name: 'Messages',

  data () {
    return {
      currentTab: '/messages'
    }
  },

  mounted () {
    // this.$store.dispatch('common/updateToolbar', {
    //  elevation: false
    // })
  },

  methods: {
  }
}
</script>

<style lang="stylus" scoped>
.my-messages
  height 100%

  &__hero
    height 100%
    display flex
    justify-content center
    align-items center
    flex-direction column
    opacity 0.4

  &__cone
    width 100px
</style>



